import { useFetcher } from '@remix-run/react';
import { ACTION_ROUTES } from '~/actions';
import { API_METHODS, API_STATES } from '~/constants/api';
import { cleanupAuthData } from '~/utils/auth';

export const useLogout = () => {
  const fetcher = useFetcher();

  const handleLogout = () => {
    fetcher.submit(
      {},
      { method: API_METHODS.POST, action: ACTION_ROUTES.logout },
    );

    if (
      fetcher.state === API_STATES.IDLE &&
      fetcher.data?.status === API_STATES.SUCCESS
    ) {
      cleanupAuthData();
    }
  };

  return { handleLogout, isLoading: fetcher.state === API_STATES.SUBMITTING };
};
