import { json } from '@remix-run/node';
import type { ActionFunction } from '@remix-run/node';
import { API_METHODS } from '~/constants/api';
import { apiHelper } from '~/network/api';
import { LOGOUT_URL } from '~/network/constants/apiUrls';

const createErrorResponse = (message: string, status: number) => json({ status: 'error', message }, { status });

export const action: ActionFunction = async ({ request }) => {
  try {
    const domainValue = process.env.API_HOST;

    const response = await apiHelper({
      url: `${domainValue}${LOGOUT_URL}`,
      method: API_METHODS.GET,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
    });

    const apiResponse = await response.json().catch(() => ({
      success: false,
      message: 'Invalid response format',
    }));

    if (!response.ok) {
      return createErrorResponse(
        apiResponse.message || 'Logout failed',
        response.status,
      );
    }

    return apiResponse.success
      ? json({ status: 'success', message: 'Logged out successfully' })
      : createErrorResponse('Failed to logout', 400);
  } catch (error) {
    console.error('Logout error:', error);
    return createErrorResponse('Error during logout', 500);
  }
};
