import styled from '@emotion/styled'; // added import

import StarFilled from '../Icons/FilledStar';
import StarHalfFilled from '../Icons/HalfFilledStar';
import { hexToRgba } from '~/utils/colorUtils';
import EmptyStar from '../Icons/EmptyStar';
import { MAX_RATING } from '~/constants/PD';

interface ProductRatingProps {
  rating: number;
  reviewCount?: number;
}

const Rating = styled.div`
  display: flex;
  align-items: center;
  padding: ${({ theme: { spacing } }) =>
    `${spacing.spacing0} ${spacing.spacing80} ${spacing.spacing50} ${spacing.spacing80}`};
`;

const Star = styled.span`
  svg path {
    fill: ${({ theme }) => hexToRgba(theme?.colors?.textPrimary, 0.92)};
  }
`;

const ReviewCount = styled.span`
  margin-left: ${({ theme }) => theme?.spacing?.spacing40};
  ${({ theme }) => theme?.typography?.subTitleMedium};
  color: ${({ theme }) => hexToRgba(theme?.colors?.textPrimary, 0.64)};
`;

const RatingText = styled.span`
  margin-left: ${({ theme }) => theme?.spacing?.spacing40};
  ${({ theme }) => theme?.typography?.titleXSmall};
  color: ${({ theme }) => hexToRgba(theme?.colors?.textPrimary, 0.92)};
`;

export default function ProductRating({
  rating,
  reviewCount,
}: ProductRatingProps) {
  const formattedReviewCount = reviewCount?.toLocaleString();
  const fullStars = Math?.floor(rating);
  const remainingStar = MAX_RATING - fullStars;
  const halfStar = rating > fullStars;
  if (!rating) return null;

  return (
    <Rating>
      {[...Array(fullStars)].map((_, index) => (
        <Star>
          <StarFilled />
        </Star>
      ))}
      {halfStar && (
        <Star>
          <StarHalfFilled />
        </Star>
      )}
      {[...Array(remainingStar - (halfStar ? 1 : 0))].map((_, index) => (
        <Star>
          <EmptyStar />
        </Star>
      ))}

      {rating && <RatingText>{rating}/5</RatingText>}

      {reviewCount && reviewCount > 0 && (
        <ReviewCount>({formattedReviewCount})</ReviewCount>
      )}
    </Rating>
  );
}
