import { useAuth } from '~/hooks/useAuth';
import NotifyMe from '~/shared/NotifyMe';
import ProductCTA from '~/shared/ProductCTA';

function ProductActions({ product, view }) {
  const {
    id: productId,
    name: productName,
    price: productPrice,
    onlyWishlistButton,
  } = product || {};
  const { user } = useAuth() || {};
  const { formKey, wishlist } = user || {};
 if (product?.inStock) {
    return (
      <ProductCTA
        productId={productId}
        // TODO -: keys needs to be updated with real data
        userDetails={{
          wishlist: wishlist,
          formKey: formKey,
        }}
        onlyWishlistButton={onlyWishlistButton}
        view={view}
      />
    );
  } else {
    return (
      <NotifyMe
        productId={productId}
        productName={productName}
        productPrice={productPrice}
      />
    );
  }
}

export default ProductActions;
