import { deleteCookie } from '~/utils/cookies';
import {
  ADDRESS_ID_FOR_DELIVERY,
  FACEBOOK_LOGIN_COOKIE,
  GOOGLE_LOGIN_COOKIE,
  LOGIN_LOCATION,
  ONE_TAP_SHOWN,
  PIN_CODE_FOR_DELIVERY,
  USER_DATA_STORAGE_KEY,
} from '~/constants/storage';
import { localStorageService } from '~/utils/storage/localStorage';

export const cleanupAuthData = () => {
  navigator.credentials.preventSilentAccess();
  deleteCookie(FACEBOOK_LOGIN_COOKIE);
  deleteCookie(GOOGLE_LOGIN_COOKIE);
  deleteCookie(USER_DATA_STORAGE_KEY);
  deleteCookie(PIN_CODE_FOR_DELIVERY);
  deleteCookie(ADDRESS_ID_FOR_DELIVERY);
  localStorageService.removeItem(LOGIN_LOCATION);
  localStorageService.removeItem(ONE_TAP_SHOWN);
  setTimeout(() => {
    window.location.reload();
  }, 1000);
};
