/** @jsxImportSource @emotion/react */
import ThumbsUp from '../assets/ThumbsUp';
import { HELPFUL, PEOPLE_FOUND_HELPFUL_TEXT } from '../constants';
import {
  Div,
  FeedbackSection,
  HelpfulText,
  PeopleCount,
  Section,
} from './styled';

interface FeedbackProps {
  likeCount: number;
  handleLikeClick: (
    reviewId: number,
    isLikedByUser: boolean,
    likeCount: number,
  ) => void;
  reviewId: number;
  isLikedByUser: boolean;
}

export default function Feedback({
  likeCount,
  handleLikeClick,
  isLikedByUser,
  reviewId,
}: FeedbackProps) {
  return (
    <FeedbackSection>
      <Div onClick={() => handleLikeClick(reviewId, isLikedByUser, likeCount)}>
        <i>
          <ThumbsUp color="black" />
        </i>{' '}
        &nbsp;<HelpfulText>{HELPFUL}</HelpfulText>
      </Div>
      {!!likeCount && (
        <Section>
          <PeopleCount>{likeCount} </PeopleCount> &nbsp;
          <span>{PEOPLE_FOUND_HELPFUL_TEXT}</span>
        </Section>
      )}
    </FeedbackSection>
  );
}
