import {
  ApiProduct,
  TransformedProduct,
  ApiRecommendationResponse,
  TransformedRecommendation,
} from '~/types/recommendations';

const getImageUrl = (media) => {
  let imageUrl = '';
  if (media) {
    imageUrl = media?.[0]?.url || '';
  }
  return imageUrl;
};

const transformProduct = (product: ApiProduct): TransformedProduct => ({
    name: product?.name,
    imageUrl: getImageUrl(product?.media),
    rating: product?.rating || 0,
    ratingCount: product?.rating_count,
    slug: product?.slug,
    brandName: product?.brand_name || '',
    packSize: product?.pack_size,
    variantType: product?.variant_type,
    id: product?.id,
    price: product?.final_price,
  });

export const transformRecommendations = (
  data: ApiRecommendationResponse,
): TransformedRecommendation[] => data?.response?.relatedProducts.map((recommendation) => ({
    title: recommendation?.title,
    products: recommendation?.products?.map(transformProduct),
    widgetType: recommendation?.widget_type,
  }));
